<template lang="pug">
  .example
    .w-flex
      .example__render.grow(:class="contentClass")
        slot
      .buttons
        w-tooltip(left)
          template(#activator="{ on }")
            w-button(
              v-on="on"
              @click="showSource = !showSource"
              lg
              icon="mdi mdi-code-tags"
              text
              color="primary")
          | View the source code
        w-tooltip(left)
          template(#activator="{ on }")
            w-button(
              v-on="on"
              @click="createCodepen"
              lg
              icon="mdi mdi-codepen"
              text
              color="primary")
          | Edit in Codepen
    w-transition-expand(y)
      .example__source(v-show="showSource")
        ssh-pre(
          v-if="$slots.html"
          language="html-vue"
          label="TEMPLATE"
          copy-button
          @copied="copied")
          template(#copy-button)
            w-icon(color="primary") mdi mdi-content-copy
          slot(name="html")
        ssh-pre(
          v-if="$slots.pug"
          language="pug"
          label="TEMPLATE (PUG)"
          copy-button
          @copied="copied")
          template(#copy-button)
            w-icon(color="primary") mdi mdi-content-copy
          slot(name="pug")
        ssh-pre(
          v-if="$slots.js"
          language="js"
          label="JS"
          copy-button
          @copied="copied")
          template(#copy-button)
            w-icon(color="primary") mdi mdi-content-copy
          slot(name="js")
        ssh-pre(
          v-if="$slots.css"
          language="css"
          label="CSS"
          copy-button
          @copied="copied")
          template(#copy-button)
            w-icon(color="primary") mdi mdi-content-copy
          slot(name="css")
        ssh-pre(
          v-if="$slots.scss"
          language="scss"
          label="SCSS"
          copy-button
          @copied="copied")
          template(#copy-button)
            w-icon(color="primary") mdi mdi-content-copy
          slot(name="scss")
        w-notification.mr5.mt-1(
          v-model="showCopied"
          transition="slide-fade-left"
          plain
          absolute
          sm
          type="success") Text copied to clipboard
</template>

<script>
export default {
  props: ['contentClass'],
  data: () => ({
    showSource: false,
    showCopied: false
  }),

  methods: {
    createCodepen (e) {
      const openEditors = [
        (!!this.$slots.html || !!this.$slots.pug) * 1,
        (!!this.$slots.css || !!this.$slots.scss) * 1,
        (!!this.$slots.js) * 1
      ]

      const cssDeps = [
        'https://unpkg.com/the-wave-ui@alpha/dist/wave-ui.css',
        'https://cdn.materialdesignicons.com/5.1.45/css/materialdesignicons.min.css'
      ]

      const jsDeps = [
        'https://unpkg.com/vue@latest/dist/vue.js',
        'https://unpkg.com/the-wave-ui@alpha/dist/wave-ui.umd.min.js'
      ]

      const slots = {
        html: this.$slots.html && this.$slots.html[0].text || '',
        pug: this.$slots.pug && this.$slots.pug[0].text || '',
        js: this.$slots.js && this.$slots.js[0].text || '',
        css: this.$slots.css && this.$slots.css[0].text || '',
        scss: this.$slots.scss && this.$slots.scss[0].text || ''
      }
      let html = ''

      if (slots.pug) {
        html = '#app\n' +
               '  w-app(block)\n    ' +
               slots.pug.replace(/\n$/, '').replace(/\n/g, '\n    ')
      }
      else {
        html = '<div id="app">\n' +
                   '  <w-app block>\n    ' +
                   slots.html.replace(/\n$/, '').replace(/\n/g, '\n    ') +
                   '\n  </w-app>\n' +
                   '</div>'
      }
      const css = '.w-app {font-family: sans-serif;padding: 24px;}\n\n' + (slots.css || slots.scss)
      const js = 'new Vue({' +
                 '\n  waveui: new WaveUI(),\n  ' +
                 slots.js.replace(/\n$/, '').replace(/\n/g, '\n  ') +
                 '\n}).$mount(\'#app\')'

      const data = {
        title: 'Wave UI Example Pen',
        editors: openEditors.join(''),
        layout: 'top',
        html,
        html_pre_processor: slots.pug ? 'pug' : 'none',
        css,
        css_pre_processor: 'scss',
        css_starter: 'reset',
        css_prefix: 'autoprefixer',
        js,
        js_pre_processor: 'babel',
        css_external: cssDeps.join(';'),
        js_external: jsDeps.join(';')
      }
      const JSONstring = JSON.stringify(data).replace(/"/g, '&quot;').replace(/'/g, '&apos;')

      e.target.insertAdjacentHTML(
        'afterend',
        `<form class="codepen-form" action="https://codepen.io/pen/define" method="POST" target="_blank">
          <input type="hidden" name="data" value="${JSONstring}">
        </form>`
      )

      const form = document.querySelector('.codepen-form')
      form.submit()
      form.remove()
    },
    copied () {
      this.showCopied = true
      setTimeout(() => (this.showCopied = false), 3000)
    }
  }
}
</script>

<style lang="scss">
.example {
  position: relative;
  margin-top: 1em;
  margin-bottom: 1em;
  border: 1px solid #eee;
  background-color: #fcfcfc;
  border-radius: 3px;
  overflow: hidden;

  .buttons {
    border-left: 1px solid #eee;
    display: flex;
    flex-direction: column;
    padding: 4px;
    background: #f0f8ff;

    .w-button {width: 30px;height: 30px;}
  }

  &__render {padding: 12px;overflow: auto;}

  &__source {position: relative;}

  .ssh-pre {
    margin: 0 !important;
    border: solid rgba(0, 0, 0, 0.1);
    border-width: 1px 0 0;
    border-radius: 0;
    line-height: 1.3;

    & + .ssh-pre {border-top-width: 3px;}
    &:last-child {border-radius: 0 0 4px 4px;}
  }

  .ssh-pre[data-label]:before {
    font-family: 'Arial Narrow', Arial, sans-serif;
    bottom: auto;
    top: 5px;
    right: 26px;
    padding: 0;
    background-color: transparent;
    border: none;
    color: #aaa;
  }

  .ssh-pre__copy {
    border: none;
    background: none;

    .w-icon {
      padding: 10px;
      transition: 0.25s;
    }
    &:hover .w-icon {background-color: rgba(35, 71, 129, 0.15);}
    &:active .w-icon {background-color: rgba(35, 71, 129, 0.25);}
  }

  .codepen-form {
    position: absolute;
    z-index: -100;
    opacity: 0;
  }
}
</style>
